#main-game {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(10, 2vw);
  grid-template-rows: repeat(20, 2vw);
}
#tetromino-preview {
  display: grid;
  grid-template-columns: repeat(4, 2vw);
  grid-template-rows: repeat(20, 2vw);
}
.cell {
  background-color: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(0, 0, 0, 0.8);
}
